/**
 * Rem-converter mixin to convert px to rem
 */
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sup,
sub,
tt,
var,
b,
u,
i,
center,
ul,
ol,
li,
dl,
dt,
dd,
table,
tr,
th,
td,
thead,
tbody,
tfoot,
caption,
form,
fieldset,
legend,
input,
textarea,
select,
label,
applet,
object,
iframe,
audio,
video,
canvas,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary {
  font-size: 100%;
  font-family: inherit;
  font-weight: inherit;
  font-style: inherit;
  line-height: inherit;
  vertical-align: baseline;
  border: 0;
  outline: 0;
  padding: 0;
  margin: 0;
}

html {
  font-size: 100%;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
}

audio:not([controls]) {
  display: none;
}

canvas {
  -ms-touch-action: double-tap-zoom;
}

[hidden] {
  display: none;
}

ul,
ol {
  list-style: none;
}

table {
  table-layout: auto;
  border-collapse: separate;
  border-spacing: 0;
  empty-cells: hide;
}

a {
  text-decoration: none;
}

body {
  font-family: "Exo 2", sans-serif;
  min-width: 320px;
}

.container {
  width: 90%;
  margin: 0 auto;
}

input,
textarea {
  -webkit-appearance: none;
}

button,
input,
select,
textarea {
  vertical-align: baseline;
  *vertical-align: middle;
  -webkit-border-radius: 0;
}

button,
input {
  line-height: normal;
  *overflow: visible;
}

table button,
table input {
  *overflow: auto;
}

button,
input[type=button],
input[type=reset],
input[type=submit] {
  cursor: pointer;
  -webkit-appearance: button;
}

textarea {
  overflow: auto;
  vertical-align: top;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  display: none;
}

input[type=search] {
  -webkit-appearance: textfield;
}

input[type=radio] {
  -webkit-appearance: radio;
}

input[type=checkbox] {
  -webkit-appearance: checkbox;
}

input::-ms-clear {
  display: none;
}

input[type=password]::-ms-reveal {
  display: none;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

* html .clearfix {
  zoom: 1;
}

*:first-child + html .clearfix {
  zoom: 1;
}

.cleaner {
  height: 0;
  line-height: 0;
  clear: both;
}

.top_header {
  background-color: #333333;
  height: 40px;
  display: flex;
  align-items: center;
  color: #ffffff;
}
.top_header nav ul {
  display: flex;
  flex-direction: row;
}
.top_header nav ul li {
  position: relative;
  height: 100%;
  width: 80px;
  margin-right: 35px;
}
.top_header nav ul li > a {
  color: #ffffff;
  text-transform: uppercase;
  position: relative;
  display: block;
  font-size: 12px;
  font-size: 0.75rem;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.top_header nav ul li > a:hover {
  color: #5aa1e1;
}
.top_header nav ul li > a span:after {
  width: 0;
  height: 0;
  border: 0.313em solid transparent;
  /* 5 */
  border-bottom: none;
  border-top-color: #ffffff;
  content: "";
  vertical-align: middle;
  display: inline-block;
  position: relative;
  right: -0.313em;
  /* 5 */
}
.top_header nav ul li > ul {
  display: none;
  flex-direction: column;
  width: 210px;
  padding: 10px 15px;
  position: absolute;
  top: 200%;
  background-color: #ffffff;
  box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.07);
}
.top_header nav ul li > ul li {
  display: block;
  width: 100%;
  border-bottom: 1px solid #b4b4b4;
  padding: 5px 0;
}
.top_header nav ul li > ul li a {
  color: #333333;
  text-transform: capitalize;
}
.top_header nav ul li > ul li a:after {
  display: none;
}
.top_header nav ul li > ul li:last-child {
  border-bottom: none;
}
.top_header nav ul li .open {
  display: flex;
}

@media screen and (max-width: 480px) {
  /*defining the hamburger*/
  #hamburger {
    display: block;
    position: absolute;
    height: 48px;
    width: 36px;
    left: 15px;
    top: 13px;
    transform: translateY(-50%);
    z-index: 200;
  }

  #hamburger .line {
    width: 36px;
    height: 2px;
    display: block;
    background-color: #fff;
    position: absolute;
    left: 0;
    transition: all 0.3s cubic-bezier(0.58, 0, 0.58, 1);
  }

  #hamburger .line:nth-child(1) {
    top: 18px;
    width: 36px;
  }

  #hamburger .line:nth-child(2) {
    top: 50%;
    margin-top: 5px;
    width: 36px;
  }

  #hamburger .line:nth-child(3) {
    top: 100%;
    margin-top: -8px;
    width: 36px;
  }

  #hamburger:hover .line:nth-child(1),
#hamburger:focus .line:nth-child(1) {
    transform: translateY(-0.5px);
  }

  #hamburger:hover .line:nth-child(3),
#hamburger:focus .line:nth-child(3) {
    transform: translateY(1px);
  }

  #hamburger.hamburger__open .line:nth-child(1) {
    transform: translateY(8px) rotate(45deg);
    width: 36px;
  }

  #hamburger.hamburger__open .line:nth-child(2) {
    opacity: 0;
  }

  #hamburger.hamburger__open .line:nth-child(3) {
    transform: translateY(-8px) rotate(-45deg);
    margin-top: -13px;
  }

  .top_header nav {
    position: absolute;
    width: 100%;
    z-index: 100;
    background: #ffffff;
    opacity: 0;
    top: 43px;
    right: 0;
    bottom: 0;
    left: 0;
    transform: translateY(-100%);
  }

  nav.showMenu {
    display: block;
    transform: translateY(-1%);
    opacity: 1;
    padding: 20px 0;
  }
  nav.showMenu ul {
    display: flex;
    flex-direction: column;
  }
  nav.showMenu ul li {
    border-bottom: 1px solid #b4b4b4;
    padding: 7px 40px;
    width: 100%;
  }
  nav.showMenu ul li a {
    display: block;
    color: #333333;
  }
  nav.showMenu ul li a span:after {
    border-top-color: #333333;
  }
  nav.showMenu ul li:last-child {
    border-bottom: none;
  }
  nav.showMenu ul li > ul {
    position: static;
    box-shadow: none;
    padding: 10px 0;
  }
  nav.showMenu ul li > ul li {
    border-bottom: none;
  }
}